.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.button {
    background-color: #f4c466;
    padding: 10px 20px;
    border: none;
    border-radius: 20px;
}

.button-clicked {
    background-color: #f0d192;
    padding: 10px 20px;
    border: none;
    border-radius: 20px;
}

.site-header {
    margin-top: 20px;
    margin-left: 100px;
    margin-right: 100px;
    margin-bottom: 20px;
    height: 50px;
}

.site-header-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}

.site-header-links {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 500px;
}

.logo {
    height: 50px;
    width: auto;
}

.error {
    color: red;
}

.text-input {
    padding: 10px;
    margin: 2px;
    border: 2px solid black;
    border-radius: 10px;
}

.no-outline:focus {
    outline: none;
    background-color: #eee;
}
